.menu-nav {
    width: 100%;
    height: 100vh;
    text-align: center;
    position: absolute;
    top: 0;
    left: 0;
    background-color: var(--primary-background-color);
    z-index: 1000;
    transform: translateX(100%);
    transition: transform var(--time-animation) var(--timing-function);
}

.menu-nav.is-open {
    transform: translateX(0);
}

.menu-nav__list {
    margin-bottom: 110px;
    .menu-nav__item + .menu-nav__item  {
            margin-top: 32px;
        }
}

.menu-nav__item .link {
    font-size: 32px;
    font-weight: 700;
    color: var(--alt-white-color);

    &:hover,
    &:focus {
         color: var(--primary-black-color);
         cursor: pointer;  
    }   
}

.mobile-menu__icon-close {
    width: 32px;
    height: 32px;
    fill: var(--alt-white-color);
}

.btn-buynow {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 12px 36px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 64px;
    font-size: 14px;
    font-weight: 600;
    color: var(--text-color);
    border: none;
    border-radius: 10px;
    background-color: var(--primary-yelow-color);

    &:hover {
        background-color: var(--alt-button-color);
    }

    &:focus {
        background-color: var(--alt-button-color);
        color: var(--primary-white-color);
        fill: #fff
    }
}

.btn-buynow__icon {
    width: 12px;
    height: 12px;
    margin-left: 4px;
}

@media screen and (min-width: 480px) {
    .menu-nav__list {
        margin-bottom: 114px;
    }

    .btn-buynow {
        margin-bottom: 88px;
    }

    .menu-nav {
        padding-top: 6px;
    }
}

@media screen and (min-width: 768px) {
    .menu-nav {
        &.container {
        padding-top: 0;
        }

        &>.header-nav {
            margin-bottom: 76px;
        }

        &>.btn-buynow {
            display: none;
        }
    }

    .menu-nav__item .link {
        font-size: 48px;
    }    
}