    padding-left: 16px;
    padding-right: 16px;
  }
}

.section {
  padding-top: 60px;
  padding-bottom: 60px;
}

@keyframes slidy {
  0% {
    left: 0%;
  }

  20% {
    left: 0%;
  }

  25% {
    left: -100%;
  }

  45% {
    left: -100%;
  }

  50% {
    left: -200.25%;
  }

  70% {
    left: -200.25%;
  }

  75% {
    left: -300.25%;
  }

  90% {
    left: -300.25%;
  }

  100% {
    left: -400.25%;
  }
}

* {
  box-sizing: border-box;
}

