  z-index: 1000;
  transition: transform var(--time-animation) var(--timing-function);
  position: absolute;
  top: 0;
  left: 0;
  transform: translateX(100%);
}

.menu-nav.is-open {
  transform: translateX(0);
}

.menu-nav__list {
