.backdrop {
  width: 100vw;
  height: 100%;
  padding-left: var(--card-gap);
  padding-right: var(--card-gap);
  opacity: 1;
  transition: opacity var(--time-animation) var(--timing-function);
  z-index: 1001;
  background-color: rgba(0, 0, 0, .2);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  visibility: inherit;
  opacity: 0;
  pointer-events: none;
}

.modal-thumb {
  border-radius: 10px;
  position: relative;
  overflow: hidden;
}

.modal {
  max-width: 280px;
  width: calc(100% - 40px);
  max-height: 100%;
  height: auto;
  text-align: center;
  background-color: var(--alt-white-color);
  border: 2px solid var(--primary-yelow-color);
  transition: transform var(--time-animation) var(--timing-function);
  border-radius: 10px;
  padding: 28px;
  position: absolute;
  top: 50%;
  left: 50%;
  overflow-y: scroll;
  transform: translate(-50%, -50%)scale(1);
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

.backdrop.is-hidden .modal {
  transform: translate(-50%, -50%)scale(1.1);
}

.modal__btn-close {
  width: 20px;
  height: 20px;
  background-color: inherit;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  border: none;
  padding: 0;
  display: block;
  position: absolute;
  top: 12px;
  right: 12px;
}

.modal__btn-close:hover, .modal__btn-close:focus {
  cursor: pointer;
}

.modal__icon-close {
  fill: #131313;
}

.modal-title {
  font-family: var(--primary-title-font);
  font-size: 16px;
  line-height: 1.5em;
}

